import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import sors from '../img/sors.gif';
import './Persik.css';
import './mis.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Persik = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			New Year 2019
		</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>
		<img className="sors" src={sors} alt="MARRY CRISTMAS"/>

				 <div class="a">
					 <a href="https://vk.com/api.devs">VK</a>
					 <a href="https://api-devs.github.io/st/map.html">FoodMap(soon)</a>
					 <a href="https://api-devs.github.io/stori.html">info</a>
				 </div>
</Panel>
);

Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Persik;



